	watch: {
		
	},
	methods: { 
		setDate(){ 
			let year = new Date().getFullYear();
			let month =new Date().getMonth() + 1 < 10? "0" + (new Date().getMonth() + 1): new Date().getMonth() + 1;
			let date =new Date().getDate() < 10? "0" + new Date().getDate(): new Date().getDate();
			let hh =new Date().getHours() < 10? "0" + new Date().getHours(): new Date().getHours();
			let mm =new Date().getMinutes() < 10? "0" + new Date().getMinutes(): new Date().getMinutes();
			let ss =new Date().getSeconds() < 10? "0" + new Date().getSeconds(): new Date().getSeconds();
			return { 
				'year' : year , 
				'month' : month , 
				'date' : date , 
				'hh' : hh , 
				'mm' : mm , 
				'ss' : ss 
			} 
		},
		updateNow() { 
			this.nowTime = this.setDate().year + "-" + this.setDate().month + "-" + this.setDate().date + " " + this.setDate().hh + ":" + this.setDate().mm + ":" + this.setDate().ss;
		},
		fnc_router_go(location){
			this.$router.push(location);
		},
		LoginConfirm() {
			if(this.username == '' || this.password == ''){
				Swal.fire({
					title:'ID와 비밀번호를 입력해주시기 바랍니다.',
					icon: 'error'
				});
				return;
			}
		
			axios.post('/back/Login/Login', null, { params: {
				clm_user_id: this.username,
				clm_user_pw: this.password
			}})
			.then(response => {
				if (response.status == '200' && response.data.clm_user_id != '') {
					this.$session.start();
					this.$session.set('user_id', response.data.clm_user_id);
					this.$session.set('user_name', response.data.clm_user_name);
					this.$session.set('user_department', response.data.clm_user_department_id);
					this.$session.set('user_department_name', response.data.clm_user_department_name);
					this.$session.set('user_authority', response.data.clm_user_authority_id);
					this.$session.set('user_authority_name', response.data.clm_user_authority_name);
					this.$session.set('user_sabun', response.data.clm_user_sabun);
					location.reload();
				}else{
					Swal.fire({
						title:'ID와 패스워드를 확인해주시기 바랍니다.',
						icon: 'error'
					});
					this.$session.destroy();
				}
			})
			.catch(error => {
				console.log(error)
			})
		},
		LogOut() {
			Swal.fire({
				title:"로그아웃 하시겠습니까?",
				text:  '',
				icon: 'question',
				showCancelButton: true,
				allowOutsideClick: false,
				confirmButtonColor: '#3085d6',
				cancelButtonColor: '#d33',
				confirmButtonText: '예',
				cancelButtonText: '아니요'
			}).then((result) => {
				if (result.isConfirmed) {
					this.$session.destroy();
					this.$router.push('mob001000');
				}
			})
			// if(confirm("로그아웃 하시겠습니까?")){
			// 	this.$session.destroy();
			// 	this.$router.push('mob001000');
			// }
		},
		fnc_bell() {
			axios.get('/back/Common/MessageList',{
				params: {
					user_id: this.user_id
				}
			})
			.then(response => {
				this.bell_items = response.data;
				if(String(response.data.length) == '0'){
					this.bell_gubun = 'mdi-bell';
				}else{
					this.bell_gubun = 'mdi-bell-ring';
				}
			});
		},
		fnc_Message_Confirm(){
			if(typeof this.bell_items[Number(this.bell_index)] == 'undefined'){
				if(this.MessageModal == true){
					this.MessageModal = false
				}
				this.bell_index = '0';
				this.bell_gubun = 'mdi-bell';
			}else{
				if(this.MessageModal == false){
					this.MessageModal = true
				}
				this.msg_id = this.bell_items[Number(this.bell_index)].clm_message_id
				this.msg_sender_name = this.bell_items[Number(this.bell_index)].clm_sender_user_name
				this.msg_sender_datetime = this.bell_items[Number(this.bell_index)].clm_sender_datetime
				this.msg_title = this.bell_items[Number(this.bell_index)].clm_message_title
				this.msg_text = this.bell_items[Number(this.bell_index)].clm_message_text

				axios.post('/back/Common/MSGConfirm', null, { params: {
					msg_id:this.msg_id
				}})
				.then(response => {
					console.log(response.data)
				});
				this.bell_index = String(Number(this.bell_index)+1)
			}
			
		},
		getMesaageList(){
			let items = axios.get('/back/SCR003011/List',{
				params: {
					search: encodeURI(this.schVal),
					search1:'',
					search2:'',
					search3:'',
					search4:this.$session.get('user_id'),
				}
				}).then(
				response => {
					items = response.data
					setTimeout(() => {
						this.me_document = items
					}, 500)
				},
			)
		},
		onClickMessageRow(event, data){
			if(this.MessageModal == false){
				this.MessageModal = true
			}
			this.msg_id = data.item.clm_message_id;
			this.msg_sender_name = data.item.clm_sender_user_name;
			this.msg_sender_datetime = data.item.clm_sender_datetime;
			this.msg_title = data.item.clm_original_message_title;
			this.msg_text = data.item.clm_original_message_text;
		},
		fnc_bell_click(){
			if(this.bell_items.length > 0){
				this.fnc_Message_Confirm();
			}else{
				let items = axios.get('/back/SCR003011/List',{
					params: {
						search: encodeURI(this.schVal),
						search1:'',
						search2:'',
						search3:'',
						search4:this.$session.get('user_id'),
					}
					}).then(
					response => {
						items = response.data	
						setTimeout(() => {
							this.me_document = items
						}, 500)
					},
				)
				this.MessageLogModal=true;
				//alert('메시지가 없습니다.');
			}
		},
		fnc_material_click(){
			this.StockModal =true;
			this.clm_s_material_type='';
			this.clm_s_half_product=['0000'];
			this.clm_m_s_material_name='';
			this.getMaterialModalList();
		},
