			this.m_product_id='';
			this.m_release_yn='N';
			
			this.m_delivery_start_date =new Date(+new Date() + 3240 * 10000).toISOString().split("T")[0];
			Date.prototype.hhmm = function(){
				var hh = this.getHours().toString();
				var mm = this.getMinutes().toString();
				return hh + ':' + (mm[1] ? mm : '0'+mm[0]);
			};
			this.m_delivery_start_time =(new Date).hhmm();
