							this.m_delivery_start_date= response.data[0].clm_delivery_start_date,
							this.m_delivery_start_time= response.data[0].clm_delivery_start_time,								
							this.m_release_id= data.item.clm_release_id,
							this.m_delivery_request_datetime= data.item.clm_delivery_request_datetime
						}
					})
					.catch(error => {
