		ProductOutReg() {	
			if(this.loading_yn=='Y'){
				alert('저장중입니다.잠시만 기다려주세요.');
				return;
			}
			this.loading_yn='Y';
			if(this.m_delivery_tel =='' ){
				alert('기사님 전화번호는 필수입니다.');
				this.loading_yn='N';
				return;
			}else if(this.m_delivery_start_date =='' ){
				alert('출발 일자는 필수입니다.');
				this.loading_yn='N';
				return;
			}else if(this.m_delivery_start_time =='' ){
				alert('출발 시간은 필수입니다.');
				this.loading_yn='N';
				return;
			}
			if(this.m_delivery_start_date != '' && this.m_delivery_start_time != ''){
				this.m_delivery_start_datetime = this.m_delivery_start_date + ' ' + this.m_delivery_start_time;
			}else{
				this.m_delivery_start_datetime = '';
			}
			axios.post('/back/SCR006004/ReleaseReg', null, { params: {
				clm_deliveryman_name: this.m_delivery_name,
				clm_delivery_tel: this.m_delivery_tel,
				clm_delivery_start_datetime : this.m_delivery_start_datetime,
				clm_delivery_car_number : this.m_delivery_car_number,
				clm_seq : this.clm_seq,
				clm_user:this.$session.get('user_id')
			}})
			.then(response => {
				if (response.status == '200') {
					this.loading_yn='N';
					Swal.fire({
						title: '정상적으로 등록 되었습니다.',
						icon: 'success'
					});
					this.selected_items1=[];
					this.outRegModal=false;
					this.getList1();
				}else{
					this.loading_yn='N';
					this.getList1();
				}
			})
			.catch(error => {
				console.log(error)
			})						
		},
